.body {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75); }

.h1 {
  font-family: Quicksand;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #1F2041; }

.h2 {
  font-family: Quicksand;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  color: #1F2041; }

.h3 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041; }

.text-field {
  width: 320px;
  height: 44px;
  background: #FFFFFF;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none; }

.text-field_theme_default {
  border: 1px solid rgba(31, 32, 65, 0.25); }

.text-field_theme_hover-focused {
  border: 1px solid rgba(31, 32, 65, 0.5); }
